@font-face {
  font-family: 'Kavoon';
  src: url('../assets/Kavoon-Regular.woff2') format('woff2');
}

header {
  background-color: #0aceff;
  border-bottom: 3px solid #84ff00;
  padding: 10px 0;
  font-family: 'Kavoon', sans-serif;
}

nav {
  display: flex;
  justify-content: center;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

li {
  margin: 0 10px;
}

a {
  color: #FFF;
  text-decoration: none;
  font-weight: bold;
  padding: 5px;
  border-radius: 5px;
}

a:hover {
  background-color: #18ff10;
}

.header-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.home-logo {
  width: 290px;
  margin-top: 4px;
  margin-bottom: 3px;
}
